@font-face{
    font-family: "NotoSansKR";
    src: url("../font/NotoSansKR.ttf") format("truetype");
  }
#film_title{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-family: "NotoSansKR";
    font-size: 15px;
    font-weight: bold;
    
}
.filmingPage{
    width:100%;
    height:100vh;
}
.top_line{
    display: grid; /* 그리드 컨테이너로 설정 */
    grid-template-columns: 0.7fr 1fr;
    justify-items:left;
    justify-content:center;
    gap: auto;
    margin:50px;
  }




.container{
    width: 100%;
    display:flex;
    justify-content: space-evenly;
    align-items:center;
    background-color: #F9F9F9;
}
#camera{
    width:100%;
}
#album{
    width:80%;
    padding:10%;
    border-radius:20%;
    border-style:none;
    flex:1;
    text-align: right;
    background-color: #F9F9F9;
}

#turtleBtn{
    border-style:none;
    width:100%;
    background:none;
    margin:10%;
    flex:1;
    text-align: center;


}

#refresh{
    width:100%;
    padding:10%;
    border-radius:20%;
    border-style:none;
    flex:1;
    text-align: right;
    background-color: #F9F9F9;
    
}

#refreshBtn{
    width:50%;
}

#screenShot{
    display:flex;
    flex-direction:column;
}

#sendPhotoBtn{
    margin:auto;
    border-radius:5vw;
    background-color:rgb(99, 200, 119);
    width: 100%;
    height:100%;
}

